@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap);
@font-face{font-family:"Abril Fatface";src:url("https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap")}input{margin:1rem;border:0.2px solid #d9cac5;padding:0.4rem;border-radius:5px}input:focus{border:0.2px solid #d9cac5}button{border:0.2px solid #d9cac5;padding:0.4rem;border-radius:5px}button:hover{cursor:pointer;background-color:#d9cac5}form{border-radius:10px;width:50vw;background:#8c7e7b;margin:1rem;padding:1rem;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}*{box-sizing:border-box;margin:0;padding:0}body{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;background-color:#f2f2f2;color:#261e1c}h1{font-family:"Abril Fatface", serif;letter-spacing:0.1rem;font-weight:500;margin-top:5rem;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;font-size:4rem;color:#8c7e7b;text-shadow:#d9cac5 0.2rem 0.2rem 0.2rem}h2{font-size:2rem}h3{font-size:1.5rem}.country{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column}.country .flag{width:25vw}

